// Bootstrap overrides for this template
.text-primary {
    color: $color-darker !important;
}

.btn-primary {
    font-weight: 700;
    @include button-variant($white, $color-darker, $color-darker);
    @include heading-font;
}

.btn-xl {
    font-size: 18px;
    font-weight: 700;
    padding: 20px 40px;
    border-radius: 3px;
    @include button-variant($white, $color-darker, $color-darker);
}



